import NHCard from '@/common/NHCard.vue';

export default {
    install(con, ...args) {
        /**
         * 测试代码
         */
        console.log('插件已开启');
        con.filter('toUpper', function (v) {
            console.log('plugin@filter');
            return v.toLocaleUpperCase();
        });
        con.mixin({
            methods: {
                fun() {
                    console.log('plugin@mixin');
                    alert('from: 插件中混入定义的函数');
                },
            },
        });
        console.log('传参', args);

        /**
         * 正式数据
         */
        // 自定义指令
        con.directive('shadow', function (el, binding) {
            /**
             * param: Object
             * color: 阴影颜色
             * radius: 盒子圆角
             * width: 盒子宽度
             */
            el.style['boxShadow'] = binding.value
                ? binding.value.color
                    ? binding.value.color
                    : '-4px -4px 8px 0 hsla(0, 0%, 100%, 0.8), 4px 4px 8px 0 rgba(1, 1, 2, 0.18)'
                : '-4px -4px 8px 0 hsla(0, 0%, 100%, 0.8), 4px 4px 8px 0 rgba(1, 1, 2, 0.18)';
            el.style['max-width'] = binding.value
                ? binding.value.width
                    ? `${binding.value.width}`
                    : '30%'
                : '30%';
            el.style['border-radius'] = binding.value
                ? binding.value.radius
                    ? `${binding.value.radius}px`
                    : '50px'
                : '50px';
            el.style['padding'] = '3px';
        });
        // flex布局
        con.directive('flex', function (el) {
            el.style['display'] = 'flex';
            el.style['justify-content'] = 'center';
            el.style['align-items'] = 'center';
            el.style['flex-direction'] = 'column';
        });
        // 头像类型图片
        con.directive('head-img', function (el) {
            el.style['max-width'] = '130px';
            el.style['max-height'] = '130px';
            el.style['border-radius'] = '50%';
            el.style['border'] = '5px solid #dddddd';
        });
        // 正常图片展示
        con.directive('img', function (el) {
            el.style['max-width'] = '400px';
            el.style['border-radius'] = '8px';
            el.style['border'] = '5px solid #dddddd';
        });
        // 信息卡片
        con.directive('card', function (el, binding) {
            /**
             * param: Number
             * 支持传入一个参数用于控制卡片的宽度
             * 默认值为400px
             */
            el.style['background-color'] = '#dddddddb';
            el.style['max-width'] = binding.value
                ? `${binding.value}px`
                : '400px';
            el.style['min-height'] = '200px';
            el.style['border-radius'] = '5px';
            el.style['padding'] = '0.5rem 18px';
            el.style['padding-bottom'] = 'unset';
            el.style['margin-top'] = '8px';
            el.style['margin-bottom'] = '2rem';
            let elP = el.querySelector('p');
            elP.style['border-bottom'] = '2px solid #ee7959';
            let elDiv = el.querySelector('div');
            elDiv.style['padding'] = 'unset';
            elDiv.style['backgroundColor'] = 'unset';
            let elDivPre = elDiv.querySelector('pre');
            // 迎合NHCard组件
            if (elDivPre) {
                elDivPre.style['white-space'] = 'break-spaces';
                elDivPre.style['marginBottom'] = 'unset';
                elDivPre.style['lineHeight'] = '20px';
                // 超链接处理 需要配合data-has-a使用
                let elDivPreA = elDivPre.querySelector('a');
                if (elDivPreA) {
                    elDivPreA.style['textDecoration'] = 'unset';
                    elDivPreA.style['color'] = '#000000';
                }
            }
        });
        // 封装自定义组件
        con.component('NHCard', NHCard);
    },
};
