import VueRouter from 'vue-router';

const routes = [
    {
        path: '/',
        component: () => import('../components/Index.vue'),
    },
    {
        path: '/props',
        component: () => import('../components/Props.vue'),
    },
    {
        path: '/mixin',
        component: () => import('../components/Mixin.vue'),
    },
    {
        path: '/plugin',
        component: () => import('../components/Plugin.vue'),
    },
    {
        path: '/scoped',
        component: () => import('../components/Scoped.vue'),
    },
    {
        path: '/component-custom-event',
        component: () => import('../components/ComCustomEvent.vue'),
    },
    {
        path: '/component-unbind-event',
        component: () => import('../components/ComCustomUnbidEvent.vue'),
    },
    {
        path: '/component-communication',
        component: () => import('../components/Communication.vue'),
    },
    {
        path: '/news-sub-publish',
        component: () => import('../components/NewsPublish.vue'),
    },
    {
        path: '/proxy',
        component: () => import('../components/Proxy.vue'),
    },
    {
        path: '/slot',
        component: () => import('../components/Slot.vue'),
    },
    {
        path: '/vuex',
        component: () => import('../components/Vuex.vue'),
    },
    {
        path: '/route',
        component: () => import('../components/Route.vue'),
        children: [
            {
                path: 'child/:tel',
                component: () => import('../common/RouteChild.vue'),
                props: true
            },
        ],
    },
    {
        path: '*',
        component: () => import('../pages/404.vue'),
    },
];

const router = new VueRouter({
    mode: 'hash',
    routes,
    linkActiveClass: 'custom-route-link',
});

export default router;
