<template>
    <div
        style="display: contents"
        app>
        <router-view />
        <div
            data-tools-nav
            v-shadow="{ radius: 8 }">
            <p class="title">导航</p>
            <ul class="nav-items">
                <li
                    class="item"
                    v-for="(item, i) in navs"
                    :key="i"
                    @click="linkTo(item.path)"
                    :ref="item.path">
                    {{ item.name }}
                </li>
            </ul>
        </div>
        <div class="signature">
            <canvas id="signature_canvas"></canvas>
        </div>
    </div>
</template>

<script>
import navs from './router/nav';

export default {
    name: 'App',
    data() {
        return {
            navs,
        };
    },
    methods: {
        linkTo(path) {
            if (path === location.pathname) {
                return false;
            }
            this.$router.push(path);
        },
        render() {
            let items = this.$refs;
            for (const item in items) {
                this.$refs[item][0].classList.remove('active-item');
            }
            items[this.pathName][0].classList.add('active-item');
        },
    },
    updated() {
        this.pathName = `/${location.pathname.split('/')[1]}`;
        this.$refs[this.pathName] ? this.render() : false;
    },
    mounted() {
        let cvs = document.getElementById('signature_canvas');
        cvs.style.width = 'inherit';
        cvs.style.height = 'inherit';
        let context = cvs.getContext('2d');
        context.font = '50px Signature';
        context.fillText('Naihe', 80, 95);
    },
};
</script>

<style scoped>
[data-tools-nav] {
    position: fixed;
    min-width: 180px;
    height: auto;
    background-color: #ffffff24;
    top: 1rem;
    right: 1rem;
    border-radius: 1rem;
    padding: 0 1rem;
    z-index: 2;
}

[data-tools-nav] .title {
    text-align: center;
}

[data-tools-nav] .nav-items {
    list-style: arabic-indic;
    line-height: 1.5rem;
}

[data-tools-nav] .item {
    padding-left: 1rem;
}

[data-tools-nav] .item:hover {
    text-decoration: underline;
    text-decoration-style: wavy;
    text-decoration-color: #bba1cb;
    cursor: pointer;
}

[data-tools-nav] .active-item {
    list-style-type: disclosure-closed;
    color: #ee7959;
}

.signature {
    position: fixed;
    right: 1rem;
    bottom: 2rem;
    width: 200px;
    height: 110px;
    font-family: 'Signature';
    z-index: 1;
}
</style>
