/**
 * vuex
 * @author Naihe
 */
import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
    actions: {
        /**
         * action里的函数通过dispatch函数触发调用
         */
        init(context) {
            console.log('------vuex------');
            // context为vuex的上下文 包含commit/dispatch等函数
            console.log('@fun init', context);
            context.commit('print');
            /**
             * es语法 参数解构
             * init({commit})
             * commit()
             * 将要执行的函数提交到mutation
             * mutation找到传递的函数名并执行
             */
        },
        loginInfo({ commit }) {
            commit('getLoginInfo');
        },
        loginIn({ commit }, params) {
            commit('loginIn', params);
        },
        processName({ state, getters }) {
            state.loginInfo.name ? getters.formatName : false;
        },
    },
    mutations: {
        /**
         * mutation里的函数通过commit函数触发调用
         */
        print(state) {
            // state可以接收vuex中的所有数据
            console.log('vuex loaded', state);
        },
        getLoginInfo({ loginInfo }) {
            if (loginInfo.name) {
                console.log(loginInfo);
            } else {
                console.log('未登录');
                alert('请先登录');
            }
        },
        loginIn({ loginInfo }, params) {
            loginInfo.name = params.username;
            loginInfo.timestamp = params.timestamp;
        },
    },
    state: {
        /**
         * 通过$store.state查看state里的所有数据
         */
        info: '我是来自vuex的数据',
        loginInfo: {
            name: null,
            timestamp: null,
        },
    },
    /**
     * 模块
     */
    modules: {
        pro: {
            // 开启命名空间
            namespaced: true,
            state: {
                proName: '华为Mate30Pro',
                proPrice: 5999,
            },
        },
    },
    /**
     * 对数据进行处理后 会将数据进行缓存 当数据再次发生改变时 将重新对数据进行处理(不需要手动调 自己会调)
     */
    getters: {
        formatName: (state) => {
            return state.loginInfo.name
                ? (state.loginInfo.name = state.loginInfo.name + '[getters]')
                : false;
        },
    },
});
