import Vue from 'vue';
import App from './App.vue';
import VueRouter from 'vue-router';
import router from './router/index';
/**
 * 引入混入
 * import mixin from './mixin';
 */
import plugin from './plugin';
import 'animate.css';
// 自定义样式
import './assets/global.css';
/**
 * vuex
 * import Vuex from 'vuex';
 */
import store from './vuex/index';

Vue.config.productionTip = false;

// 设置图片资源的根地址
Vue.prototype.baseimg = process.env.BASE_URL + 'image';
// 设置字体资源的根地址
Vue.prototype.basefont = process.env.BASE_URL + 'font';

Vue.use(VueRouter);
/**
 * 使用混入
 * Vue.mixin(mixin);
 */
Vue.use(plugin, 'naihe', 18, '一个阳光大男孩');
// Vue.use(Vuex);

new Vue({
    render: (h) => h(App),
    router,
    store,
    beforeCreate() {
        // 全局事件总线
        Vue.prototype.$worker = this;
    },
    mounted() {
        console.log(this.$store);
        this.$store.dispatch('init');
    },
}).$mount('#root');
